<template>
<div class="checkout">

      <div id="app" v-cloak>
       
        <svg style="position: absolute; width: 0; height: 0; overflow: hidden;" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
          <defs>
            <symbol id="icon-add" viewBox="0 0 32 32">
              <title>add2</title>
              <path class="path1" d="M15 17h-13.664c-0.554 0-1.002-0.446-1.002-1 0-0.552 0.452-1 1.002-1h13.664v-13.664c0-0.554 0.446-1.002 1-1.002 0.552 0 1 0.452 1 1.002v13.664h13.664c0.554 0 1.002 0.446 1.002 1 0 0.552-0.452 1-1.002 1h-13.664v13.664c0 0.554-0.446 1.002-1 1.002-0.552 0-1-0.452-1-1.002v-13.664z"></path>
            </symbol>
            <symbol id="icon-ok" viewBox="0 0 39 32">
              <title>ok</title>
              <path class="path1" d="M14.084 20.656l-7.845-9.282c-1.288-1.482-3.534-1.639-5.016-0.351s-1.639 3.534-0.351 5.016l10.697 12.306c1.451 1.669 4.057 1.623 5.448-0.096l18.168-22.456c1.235-1.527 0.999-3.765-0.528-5.001s-3.765-0.999-5.001 0.528l-15.573 19.337z"></path>
            </symbol>
            <symbol id="icon-edit" viewBox="0 0 32 32">
              <title>edit</title>
              <path class="path1" d="M25.599 11.292l-4.892-4.892 3.825-3.825 4.892 4.892-3.825 3.825zM4.732 23.308l3.959 3.959-5.939 1.98 1.98-5.939zM10.666 26.225l-4.892-4.892 13.425-13.425 4.892 4.892-13.425 13.425zM31.687 6.713l-6.4-6.4c-0.417-0.417-1.091-0.417-1.508 0l-20.267 20.267c-0.114 0.115-0.191 0.25-0.242 0.393-0.003 0.009-0.012 0.015-0.015 0.025l-3.2 9.6c-0.128 0.383-0.029 0.806 0.257 1.091 0.203 0.204 0.476 0.313 0.754 0.313 0.112 0 0.227-0.017 0.337-0.054l9.6-3.2c0.011-0.003 0.017-0.013 0.027-0.016 0.142-0.052 0.276-0.128 0.39-0.242l20.267-20.267c0.417-0.416 0.417-1.091 0-1.508v0z"></path>
            </symbol>
            <symbol id="icon-del" viewBox="0 0 26 32">
              <title>delete</title>
              <path class="path1" d="M17.723 28c0.543 0 0.984-0.448 0.984-1v-12c0-0.552-0.441-1-0.984-1s-0.985 0.448-0.985 1v12c0 0.552 0.441 1 0.985 1v0zM7.877 28c0.543 0 0.984-0.448 0.984-1v-12c0-0.552-0.441-1-0.984-1s-0.985 0.448-0.985 1v12c0 0.552 0.441 1 0.985 1v0zM12.8 28c0.543 0 0.985-0.448 0.985-1v-12c0-0.552-0.441-1-0.985-1s-0.984 0.448-0.984 1v12c0 0.552 0.441 1 0.984 1v0zM23.631 4h-5.908v-2c0-1.104-0.882-2-1.969-2h-5.908c-1.087 0-1.969 0.896-1.969 2v2h-5.908c-1.087 0-1.969 0.896-1.969 2v2c0 1.104 0.882 2 1.969 2v18c0 2.208 1.765 4 3.939 4h13.784c2.174 0 3.938-1.792 3.938-4v-18c1.087 0 1.969-0.896 1.969-2v-2c0-1.104-0.882-2-1.969-2v0zM9.846 3c0-0.552 0.441-1 0.984-1h3.938c0.544 0 0.985 0.448 0.985 1v1h-5.908v-1zM21.662 28c0 1.104-0.882 2-1.969 2h-13.784c-1.087 0-1.97-0.896-1.97-2v-18h17.723v18zM22.646 8h-19.692c-0.543 0-0.985-0.448-0.985-1s0.441-1 0.985-1h19.692c0.543 0 0.984 0.448 0.984 1s-0.441 1-0.984 1v0z"></path>
            </symbol>
            <symbol id="icon-clock" viewBox="0 0 32 32">
              <title>clock</title>
              <path class="path1" d="M29.333 16c0-7.364-5.97-13.333-13.333-13.333s-13.333 5.97-13.333 13.333c0 7.364 5.97 13.333 13.333 13.333s13.333-5.97 13.333-13.333v0 0 0 0 0 0zM0 16c0-8.837 7.163-16 16-16s16 7.163 16 16c0 8.837-7.163 16-16 16s-16-7.163-16-16zM14.667 14.667v1.333h2.667v-10.667h-2.667v9.333zM24 18.667h1.333v-2.667h-10.667v2.667h9.333z"></path>
            </symbol>
          </defs>
        </svg>
        <div class="container">
          <div class="cart">
            <div class="checkout-title">
              <span>购物车</span>
            </div>

            <!-- table -->
            <div class="item-list-wrap">
              <div class="cart-item">
                <div class="cart-item-head">
                  <ul>
                    <li>商品信息</li>
                    <li>商品单价</li>
                    <li>商品数量</li>
                    <li>总金额</li>
                    <li>编辑</li>
                  </ul>
                </div>
                <ul class="cart-item-list">
                  <!--遍历data内的productList数组，用item来代替数组内的每一项-->
                  <li v-for="(item,index) in productList">
                    <div class="cart-tab-1">
                      <div class="cart-item-check">
                        <!-- 商品左边的小圆圈，v-for 里的item 也可以作为参数 -->
                        <a href="javascript:void 0" class="item-check-btn" :class="{'check':item.ischecked}" @click="selectedItem(item)">
                          <svg class="icon icon-ok"><use xlink:href="#icon-ok"></use></svg>
              
                      </a>
                      </div>
                      <div class="cart-item-pic">
                        <!--遍历，绑定src的值到获取到的json图片地址-->
                        <img alt="烟" v-bind:src="item.productImage">
                      </div>
                      <div class="cart-item-title">
                        <!--遍历，绑定文本值到取到的json的产品名称-->
                        <div class="item-name">{{ item.productName }}</div>
                      </div>
                      <div class="item-include">
                        <dl>
                          <dt>赠送:</dt>
                         <!-- v-for里面嵌套遍历，注意item-->
                          <dd v-for="part in item.parts">{{ part.partsName }}</dd>
                        </dl>
                      </div>
                    </div>
                    <div class="cart-tab-2">
                      <!--遍历，绑定文本值到取到的json的产品价格，并用过滤去filters进行处理-->
                      <div class="item-price">{{ item.productPrice | chineseYuan }}</div>
                    </div>
                    <div class="cart-tab-3">
                      <div class="item-quantity">
                        <div class="select-self select-self-open">
                          <div class="quantity">
                            <a href="javascript:void 0" v-on:click="changeQuantity(item,-1)"> - </a>
                            <input type="text" value="0" disabled style="max-width: 66px" v-model="item.productQuantity">
                            <a href="javascript:void 0" v-on:click="changeQuantity(item,1)"> + </a>
                          </div>
                        </div>
                        <div class="item-stock">有货</div>
                      </div>
                    </div>
                    <div class="cart-tab-4">
                      <!--将价格和数量的值进行计算（此处可用 '计算属性' 代替，但是由于计算比较简单，不用亦可）-->
                      <div class="item-price-total">{{ item.productPrice * item.productQuantity | chineseYuan }}</div>
                    </div>
                    <div class="cart-tab-5">
                      <div class="cart-item-operation">
                        <a href="javascript:void 0" class="item-edit-btn" @click="removeConfirm(item)"> 
                          <svg class="icon icon-del"><use xlink:href="#icon-del" ></use></svg>
                        </a>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>

            <!-- footer -->
            <div class="cart-foot-wrap">
              <div class="cart-foot-l">
                <div class="item-all-check" :class="{'check':selectedAll}" >
                  <a href="javascript:void 0">
                    <span class="item-check-btn"  @click="checkAll(true)">
                      <svg class="icon icon-ok"><use xlink:href="#icon-ok"></use></svg>
                    </span>
                    <span class="item-check-text">全选</span>
                  </a>
                </div>
                <div class="item-all-del" :class="{'check': selectedAll}">
                  <a href="javascript:void 0" class="item-del-btn"   @click="checkAll(false)">
                    <span>取消全选</span>
                  </a>
                </div>
              </div>
              <div class="cart-foot-r">
                <div class="item-total">
                  <!--全部商品合集金额-->
                                    合计金额: <span class="total-price">{{ totalMoney | chineseYuan }}</span>
                </div>
                <div class="next-btn-wrap" @click='gotoAddress'>
                  <a href="javascrit:;" class="btn btn--red" style="width: 200px">结账</a>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="md-modal modal-msg md-modal-transition" :class="{ 'mdshow':deleteClass }" id="showModal">
          <div class="md-modal-inner">
            <div class="md-top">
              <button class="md-close" @click="deleteClass=false">关闭</button>
            </div>
            <div class="md-content">
              <div class="confirm-tips">
                <p id="cusLanInfo">你确认删除此订单信息吗?</p>
              </div>
              <div class="btn-wrap col-2">
                <button class="btn btn--m" id="btnModalConfirm" @click="removeClass">Yes</button>
                <button class="btn btn--m btn--red" id="btnModalCancel" @click="deleteClass=false">No</button>
              </div>
            </div>
          </div>
        </div>
        <div class="md-overlay" v-if="deleteClass"></div>
      </div>
    </div>

</template>

<script>
   import '../assets/css/base.css'
   import '../assets/css/checkout.css'
   import '../assets/css/modal.css'

  export default {
    name:'id',
   data(){
    return {
           productList:[],
           totalMoney:0,
           selectedAll:false,
           deleteClass:false,
           currentClass:''
    }
   
  },
  filters:{
    //Vue 2.0瘦身了很多自带filter,能用原生写的全部清理掉
    chineseYuan:function(val){
      if(!val) return '0';
      return '¥ ' + val.toFixed(2);//真正项目中需要后代反馈金额，JS会导致金额误差
    }
  },
  computed:{
    
  },
  //钩子函数，当所有DOM挂载在页面上时，加载此方法，相当于window.onload=function(){}
  mounted:function(){
    //需要用$nextTick来保证所有节点挂载后才执行方法
    this.$nextTick(function(){
      this.cartView();
    })
  },
  methods:{
    cartView:function(){
      var _this = this;
      //Vue.source插件，目前已经被axios代替,向后台的json文件要数据

   // this.$http.get('/api/cartData',{'id':123}).then(function(res){
      this.$http.get('./static/data/cartData.json',{'id':123}).then(function(res){
       // console.log(res);
        _this.productList = res.body.result.list;  
          _this.productList.forEach(function(val,key){
            _this.$set(val,'ischecked',false);
          });
          
      });
    },
    changeQuantity:function(product,type){
      if (type>0) {
        //加号增加数量
        product.productQuantity++;
        this.calcTotalmoney();//数量变动重新计算总金额
      } else{
        //减号减少数量，但是因为有删除按钮所以最小数量为1
        if(product.productQuantity < 2){
          product.productQuantity = 1;
        }else{
          product.productQuantity--;
          this.calcTotalmoney();//数量变动重新计算总金额
        }
      }
    },
    selectedItem:function(item){
      
      if(typeof item.ischecked === 'undefined'){
        //局部$set方法，在item里注册ischecked属性，赋值为true
        this.$set(item,'ischecked',true);
      }else {
        //点击反转属性值
        item.ischecked = !item.ischecked;
      };
     this.checkAll(); //检查是否全选
    },
    checkAll:function(statu){
     
      if(statu===undefined){
        var statu = true;
            this.productList.forEach(function(val,index){
                statu= statu&&val.ischecked;
           
              });   
           console.log('11111')
         this.selectedAll = statu;
      }else{
            //根据传参决定是全选还是取消全选
             console.log('22222')
      this.selectedAll = statu;
      var _this = this;//用ES5方法解决this指向问题
      this.productList.forEach(function(val,index){
       
         val.ischecked = _this.selectedAll;
        
       });
      }
    
      this.calcTotalmoney();// 全选/非全选 商品重新计算总金额
    },

    calcTotalmoney:function(){
      var _this = this;//用ES5方法解决this指向问题
      //每次计算前必须清理，防止出现累计计算
      this.totalMoney = 0;
      this.productList.forEach(function(val,index){
        if(val.ischecked){
          _this.totalMoney += val.productPrice * val.productQuantity;
        }
      });
    },
    removeConfirm:function(item){
      this.deleteClass = true;
      this.currentClass = item;
    },
    removeClass:function(){
      //indexOf方法接受一个值，在数组中进行检索这个值是否存在，这个值可以使字符串、数字、和对象
      var index = this.productList.indexOf(this.currentClass);
      this.productList.splice(index,1);
      this.deleteClass = false;
      this.calcTotalmoney();//删除商品后重新计算总金额
    },
    gotoAddress: function(){
      console.log('address....')
      this.$router.push({path:'/Address'})
    }
  }
  }


 

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }
</style>



